    </el-form>
    <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" max-height="600" :summary-method="getSummaries" show-summary>
      <el-table-column :show-overflow-tooltip="true" type="index" align="center" label="序号" width="46">
