    <div class="refundauditFoot">
      <span>创建人：{{params.add_user_name}}</span>
      <span>创建时间：{{params.add_time}}</span>
    </div>
    <div slot="footer" class="dialog-footer">
      <template v-if="params.auditor_status == 1">
        <el-button type="primary" @click="audit('auditorPassOtherDelivery')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('auditorInvalidOtherDelivery')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <template v-if="params.auditor_status == 2">
        <el-button @click="audit('unAuditorPassOtherDelivery')" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button @click="audit('auditorInvalidOtherDelivery')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <template v-if="params.auditor_status == 3 || (authority.b_pass != 1 && authority.b_nopass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
