<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "instorageOtherDeliveryOtherDeliverySee",
  data() {
    return {
      authority: {},
      params: {},
      TableData: {
        tableData: []
      }
    };
  },
  computed: {
    ...mapGetters(["otherDeliverySeeGet"])
  },
  methods: {
    ...mapActions([
      "otherDeliveryAddACT",
      "otherDeliveryIndexACT",
      "otherDeliverySeeACT"
    ]),
    goBack() {
      this.$router.push({
        path: "/inventory/otherdelivery"
      });
    },
    audit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          other_delivery_id: this.params.other_delivery_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        if (column.property === "delivery_goods_num") {
          const values = data.map(item => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index]);
        }
        if (column.property === "postage_price") {
          const values = data.map(item => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index])
            .toFixed(2)
            .toString()
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
        }
      });
      return sums;
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$axMethod
      .getOtherDeliveryInfo({
        other_delivery_id: this.otherDeliverySeeGet.other_delivery_id
      })
      .then(res => {
        if (res.data.code == 200) {
          this.$set(this, "params", res.data.data);
          this.TableData.tableData = res.data.data.goods_info;
        }
      });
    // this.$axMethod
    //   .checkDelivery({
    //     other_delivery_id: this.otherDeliveryIndexGet.other_delivery_id
    //   })
    //   .then(res => {
    //     if (res.data.code == 200) {
    //       this.TableData.tableData = res.data.data.goods_info;
    //     }
    //   });
  }
};
</script>